// section smile part start
.section-smile {
    position: relative;
    z-index: 1;
    overflow: hidden;

    & .container {
        display: flex;
        flex-direction: column;
        align-items: center;

        & .btn {
            width: 100%;
            margin-top: 50px;

            @media (min-width: 576px) {
                width: 390px;
            }

            @media (min-width: 720px) {
                margin-top: 55px;
            }

            @media (min-width: 1200px) {
                width: 31vw;
            }

            @media (min-width: 1680px) {
                width: 517px;
            }
        }
    }

    & .swiper-smiles {
        margin-top: 16px;

        @media (min-width: 720px) {
            margin-top: 34px;
        }

        @media (min-width: 1200px) {
            max-width: 100%;
            margin-top: 40px;
        }

        & .swiper-wrapper {
            align-items: stretch;
            margin: 0 auto;
        }

        & .swiper-slide {
            position: relative;
            z-index: 1;
            width: fit-content !important;
        }

        & .swiper-pagination {
            position: relative;
            top: unset;
            bottom: unset;
            left: unset;
            margin-top: 26px;

            @media (min-width: 720px) {
                margin-top: 36px;
            }

            @media (min-width: 1200px) {
