}

.smile-card {
    position: relative;
    z-index: 1;
    display: block;
    height: 100%;
    width: 94vw;

    @media (min-width: 720px) {
        width: 54vw;
    }

    @media (min-width: 1200px) {
        width: 31vw;
    }

    @media (min-width: 1680px) {
        width: 520px;
    }

    & .smile-title {
        font-style: normal;
        font-weight: 400;
        font-size: 14px;
        line-height: 18px;
        color: #585a60;

        @media (min-width: 720px) {
            font-size: 22px;
            line-height: 27.5px;
        }
    }

    & .smile-images {
        position: relative;
        z-index: 5;
        width: 100%;
        aspect-ratio: 2 / 1.25;
        pointer-events: all;
        margin-top: 10px;

        @media (min-width: 720px) {
            aspect-ratio: 2 / 1.5;
        }

        & img {
            object-fit: cover;
        }

        & img-comparison-slider {
            visibility: hidden;
            outline: none;
            border: 0;

            & [slot="second"] {
                display: none;
            }

            &.rendered {
                visibility: inherit;
                width: 100%;
                height: 100%;

                & [slot="first"],
                & [slot="second"] {
                    width: 100%;
                    height: 100%;
                    object-fit: cover;
                }

                & [slot="second"] {
                    display: unset;
                }
            }
        }

        & *:focus-visible {
            outline: none;
        }
    }

    & .smile-name {
        font-style: normal;
        font-weight: 400;
        font-size: 16px;
        line-height: 20px;
        color: var(--blue);
        margin-top: 3px;
        text-decoration: underline;

        &:hover {
            text-decoration: none;
        }

        @media (min-width: 720px) {
            font-size: 20px;
            line-height: 25px;
        }
    }

    & .smile-doctor {
        font-style: normal;
        font-weight: 400;
        font-size: 12px;
        line-height: 15px;
        color: #585a60;
        margin-top: 18px;

        @media (min-width: 720px) {
            font-size: 16px;
            line-height: 20px;
            margin-top: 6px;
        }
    }
}

// section smile part end

// section reviews part start
