        }

        & .swiper-slide {
            position: relative;
            z-index: 1;
            width: fit-content !important;
            height: auto;
        }

        & .swiper-pagination {
            position: relative;
            top: unset;
            bottom: unset;
            left: unset;
            margin-top: 26px;

            @media (min-width: 720px) {
                margin-top: 36px;
            }

            @media (min-width: 1200px) {
                margin-top: 40px;
            }
        }

        & ~ .btn {
            width: 100%;
            margin-top: 50px;

            @media (min-width: 576px) {
                width: 340px;
            }

            @media (min-width: 720px) {
                margin-top: 55px;
            }

            @media (min-width: 1200px) {
                width: 31vw;
            }

            @media (min-width: 1680px) {
                width: 517px;
            }
        }
    }
}

.reviews {
    &-filters {
        display: none;
        flex-direction: row;
        align-items: center;
        justify-content: space-between;
        width: 100%;
        background-color: #ffffff;
        border-radius: 10px;
        overflow: hidden;

        @media (min-width: 1200px) {
            display: flex;
            max-width: 1600px;
            margin-top: 40px;
        }
    }

    &-grade {
        flex: 1 0 auto;
        font-style: normal;
        font-weight: 500;
        font-size: 12px;
        line-height: 15px;
        color: #585a60;
    }

    &-total {
        font-style: normal;
        font-weight: 500;
        font-size: 20px;
        line-height: 25px;
        color: #2c356d;
    }

    &-btn {
        display: flex;
        flex-direction: column;
        align-items: stretch;
        justify-content: space-around;
        row-gap: 4px;
        width: 100%;
        min-height: 60px;
        padding: 3px 10px;
        font-size: 16px;
        font-weight: normal;
        line-height: 120%;
        color: var(--black);
        text-align: left;
        text-decoration: none;
        vertical-align: middle;
        cursor: pointer;
        user-select: none;
        text-transform: none;
        border-radius: 10px;
        border: 0;
        background-color: #ffffff;
        white-space: nowrap;
        transition: 0.4s ease;

        @media (min-width: 1200px) {
            padding: 8px 3%;
        }

        &:focus:active {
            opacity: 0.5;
        }

        &.active {
            background-color: rgba(17, 118, 234, 0.2);
        }

        & img {
            max-width: fit-content;
            max-height: 13px;
            object-fit: contain;

            @media (min-width: 1200px) {
                max-height: 25px;
            }
        }
    }
}

.review {
    &-card {
        position: relative;
        z-index: 1;
        display: flex;
        flex-direction: column;
        max-width: 94vw;
        width: 100%;
        height: 100%;
        background-color: #ffffff;
        color: #585a60;
        padding: 35px 20px;
        border-radius: 10px;
        text-decoration: none;
        transition: 0.4s ease;

        @media (min-width: 720px) {
            max-width: unset;
            width: 520px;
            padding: 35px 30px;
        }

        @media (min-width: 1200px) {
            width: 31vw;
            padding: 30px;
        }

        @media (min-width: 1680px) {
            width: 520px;
        }

        & .review-rating {
            margin-bottom: 8px;
        }

        &:hover {
            box-shadow: 0px 4px 13px rgba(0, 0, 0, 0.25);

            & .review-source {
                color: var(--blue);
            }
        }
    }

    &-name {
        font-style: normal;
        font-weight: 500;
        font-size: 16px;
        line-height: 20px;
        color: #585a60;
        margin-bottom: 10px;

        @media (min-width: 720px) {
            font-size: 20px;
            line-height: 25px;
            margin-bottom: 5px;
        }
    }

    &-date {
        font-weight: 400;
        font-size: 12px;
        line-height: 15px;
        margin: unset;
    }

    &-description {
        flex: 1;
        -webkit-line-clamp: 7;
        display: -webkit-box;
        -webkit-box-orient: vertical;
        overflow: hidden;
        font-weight: 300;
        font-size: 14px;
        line-height: 18px;
        margin: 14px auto 18px;
    }

    &-rating {
        display: flex;
        flex-direction: row;
        flex-wrap: nowrap;
        gap: 5px;

        & i {
            display: flex;
            font-size: 15px;
        }
    }

    &-doctor {
        width: fit-content;
        font-style: normal;
        font-weight: 500;
        font-size: 16px;
        line-height: 20px;
        color: #585a60;

        & a {
            color: var(--blue);
            text-decoration: none;
            border-bottom: 1px solid var(--blue);

            &:hover {
                border-color: transparent;
            }
        }

        @media (min-width: 720px) {
            font-size: 18px;
            line-height: 22.5px;
        }
    }

    &-source {
        font-style: normal;
        font-weight: 500;
        font-size: 12px;
        line-height: 15px;
        color: #7f8186;
        text-decoration: none;
        margin-top: 10px;
        transition: 0.4s ease;

        @media (min-width: 1200px) {
            font-size: 14px;
            line-height: 17.5px;
            margin-top: 7px;
        }

        &:hover {
            color: var(--blue);
        }
    }
}

// section reviews part end

// section result part start
.section-result {
    position: relative;
    z-index: 1;
    display: block;
    padding: 67px 0 37px;
    overflow: hidden;

    @media (min-width: 720px) {
        padding: 70px 0 60px;
    }

    @media (min-width: 1200px) {
        padding: 70px 0 80px;
    }

    &-content {
        display: flex;
        flex-direction: column;
        row-gap: 25px;
        column-gap: 25px;
        width: 100%;
        margin-top: 20px;

        @media (min-width: 720px) {
            margin-top: 30px;
        }

        @media (min-width: 1200px) {
            flex-direction: row;
            margin-top: 34px;
        }
    }

    & .swiper-result {
        @media (min-width: 1200px) {
            max-width: 60%;
        }

        & .swiper-wrapper {
            align-items: stretch;
            min-width: 100%;
        }

        & .swiper-slide {
            position: relative;
            z-index: 1;
            display: flex;
            flex-direction: column;
            height: auto !important;
        }

        & .swiper-pagination {
            position: relative;
            top: unset;
            bottom: unset;
            left: unset;
            margin-top: 26px;

            @media (min-width: 720px) {
                margin-top: 36px;
            }

            @media (min-width: 1200px) {
                margin-top: 40px;
            }
        }
    }
}

.results-image {
    display: none;
    flex: 1;
    width: 30%;
    max-width: 629px;
    border-radius: 14px;
    overflow: hidden;

    @media (min-width: 1200px) {
        display: flex;
        flex-direction: column;
    }

    & > * {
        width: 100%;
        height: 100%;
        object-fit: cover;
    }
}

.result-card {
    display: flex;
    flex-direction: column;
    align-items: center;
    height: 100%;
    padding: 20px 5px 30px;
    border: 1px solid rgba(44, 53, 109, 0.3);
    border-radius: 10px;
    background: #ffffff;

    @media (min-width: 720px) {
        padding: 38px 12px 30px;
    }

    @media (min-width: 1200px) {
        padding: 45px 15px 30px;
    }

    & svg {
        width: fit-content;
        height: 53px;
        object-fit: contain;
        margin-bottom: 13px;

        @media (min-width: 720px) {
            height: 75px;
        }
    }

    & p {
        margin: unset;
        font-size: 12px;
        line-height: 15px;
        text-align: center;

        @media (min-width: 720px) {
            font-size: 16px;
            line-height: 20px;
        }
    }
}

// section result part end

// section about part start
.section-about {
    position: relative;
    z-index: 1;
    column-gap: 20px;
    row-gap: 20px;
    background-color: #eef5fe;
    padding: 28px 0;
    overflow: hidden;

    @media screen and (max-width: 1199.98px) {
        display: flex;
        flex-direction: column;
    }

    @media (min-width: 720px) {
        padding: 66px 0 52px;
    }

    @media (min-width: 1200px) {
        display: grid;
        grid-template-columns: repeat(2, 40% 60%);
        grid-template-rows: repeat(2, auto);
        padding: unset;
    }

    & .section-title {
        padding: 0 16px;

        @media screen and (max-width: 1199.98px) {
            order: -1;
        }

        @media (min-width: 720px) {
            padding: 0 12%;
        }

        @media (min-width: 1200px) {
            grid-area: 1 / 2 / 2 / 3;
            padding: 0 8%;
            margin: 43px 0 0 0;
        }

        @media (min-width: 1400px) {
            padding: 0 10%;
        }
    }

    &-image {
        @media (min-width: 1200px) {
            grid-area: 1 / 1 / 6 / 2;
        }

        & > * {
            width: 100%;
            height: 100%;
            object-fit: cover;
        }
    }

    &-content {
        padding: 0 16px;
        font-style: normal;
        font-weight: 400;
        font-size: 12px;
        line-height: 15px;
        color: #585a60;

        @media (min-width: 720px) {
            font-size: 16px;
            line-height: 20px;
            padding: 0 12%;
        }

        @media (min-width: 1200px) {
            grid-area: 2 / 2 / 3 / 3;
            padding: 0 8%;
        }

        @media (min-width: 1400px) {
            padding: 0 10%;
        }
    }
}

.about-list {
    list-style: none;
    padding-left: 0;

    & li {
        position: relative;
        margin-bottom: 25px;
        padding-left: 45px;
        font-size: initial;
        line-height: initial;

        &::before {
            content: url('data:image/svg+xml,<svg width="25" height="25" viewBox="0 0 25 25" fill="none" xmlns="http://www.w3.org/2000/svg"><path d="M12.5 0C10.8756 0 9.55882 1.31684 9.55882 2.94118V9.55882H2.94118C1.31684 9.55882 0 10.8756 0 12.5C0 14.1244 1.31684 15.4412 2.94118 15.4412H9.55882V22.0588C9.55882 23.6832 10.8756 25 12.5 25C14.1244 25 15.4412 23.6832 15.4412 22.0588V15.4412H22.0588C23.6832 15.4412 25 14.1244 25 12.5C25 10.8756 23.6832 9.55882 22.0588 9.55882H15.4412V2.94118C15.4412 1.31684 14.1244 0 12.5 0Z" fill="%233981F1" /></svg>');
            position: absolute;
            left: 0;
            top: 50%;
            transform: translate(0, -50%);
        }
    }
}

// section about part end

// section consult part start
.section-consult {
    position: relative;
    z-index: 1;
    padding: 40px 0;
    background: linear-gradient(180deg, #1176ea 0%, rgba(17, 118, 234, 0.8) 100%);

    @media (min-width: 720px) {
        padding: 60px 0;
    }

    @media (min-width: 1200px) {
        padding: 50px 0;
        background-image: linear-gradient(195deg, var(--blue) 0%, #1b66bd 100%);
    }

    & h2 {
        color: #ffffff;
    }

    & .consult-form {
        padding: 0 8.335%;

        @media screen and (max-width: 1199.98px) {
            & > * {
                &:nth-child(1) {
                    order: -1;
                }

                &:nth-child(2) {
                    order: 1;
                }

                &:nth-child(3) {
                    order: 5;
                }

                &:nth-child(4) {
                    order: 0;
                }

                &:nth-child(5) {
                    order: 4;
                }

                &:nth-child(6) {
                    order: 5;
                }

                &:nth-child(7) {
                    order: 6;
                }
            }
        }

        @media (min-width: 720px) {
            padding: 0 16.665%;
        }

        @media (min-width: 1200px) {
            padding: 0;
        }
    }
}

.consult-form {
    display: flex;
    flex-direction: column;
    column-gap: 5px;
    row-gap: 5px;
    margin-top: 24px;

    @media (min-width: 720px) {
        column-gap: 12px;
        row-gap: 12px;
        margin-top: 40px;
    }

    @media (min-width: 1200px) {
        display: grid;
        grid-template-columns: repeat(3, 1fr);
        grid-template-rows: repeat(3, auto);
        column-gap: 8px;
        row-gap: 8px;

        & > * {
            &:nth-child(1) {
                grid-area: 1 / 1 / 2 / 2;
            }

            &:nth-child(2) {
                grid-area: 1 / 2 / 2 / 3;
            }

            &:nth-child(3) {
                grid-area: 1 / 3 / 3 / 4;
            }

            &:nth-child(4) {
                grid-area: 2 / 1 / 3 / 2;
            }

            &:nth-child(5) {
                grid-area: 2 / 2 / 3 / 3;
            }

            &:nth-child(6) {
                grid-area: 3 / 2 / 4 / 4;
            }
        }
    }

    & .input-textarea {
        resize: none;
    }

    &-agree {
        @media (min-width: 1200px) {
            align-self: center;
        }

        & * {
            color: #ffffff;
        }
    }

    &-link {
        text-decoration: underline;
        text-decoration-skip-ink: none;

        &:hover {
            text-decoration: none;
        }
    }

    & .btn {
        width: fit-content;
        padding: 16px 23%;
        margin: auto;

        @media (min-width: 720px) {
            padding: 20px 20%;
        }

        @media (min-width: 1200px) {
            margin: unset;
            padding: 20px 136px;
        }
    }

    &-submit {
        display: flex;
        flex-direction: column;
        align-items: stretch;
        column-gap: 15px;
        row-gap: 15px;
        margin-top: 15px;

        @media (min-width: 720px) {
            column-gap: 35px;
            row-gap: 35px;
            margin-top: 7px;
        }

        @media (min-width: 1200px) {
            flex-direction: row;
            align-items: center;
            justify-content: flex-end;
            column-gap: 24px;
            row-gap: 24px;
            margin-top: 16px;
        }
    }
}

// section consult part end
